// Ï≤≠ÏïΩ Í≤ΩÏüÅÎ•† Îç∞Ïù¥ÌÑ∞ (ÏµúÍ∑º Î∂ÑÏñë Îã®ÏßÄ)
export const generateSubscriptionData = () => {
    const recentProjects = [
        { name: 'ÎûòÎØ∏Ïïà ÏõêÎ≤†ÏùºÎ¶¨', region: 'ÏÑúÏö∏ ÏÑúÏ¥à', date: '2024.11', units: 1200 },
        { name: 'ÎîîÏóêÏù¥Ïπò ÌÅ¥ÎûòÏä§Ìä∏', region: 'ÏÑúÏö∏ Í∞ïÎèô', date: '2024.10', units: 850 },
        { name: 'ÌûêÏä§ÌÖåÏù¥Ìä∏ Í¥ëÍµê', region: 'Í≤ΩÍ∏∞ ÏàòÏõê', date: '2024.09', units: 2200 },
        { name: 'Î°ØÎç∞Ï∫êÏä¨ ÏãúÍ∑∏ÎãàÏ≤ò', region: 'ÏÑúÏö∏ ÏÜ°Ìåå', date: '2024.08', units: 750 },
        { name: 'ÏûêÏù¥ ÌîÑÎ†àÏä§Ìã∞ÏßÄ', region: 'ÏÑúÏö∏ Ïö©ÏÇ∞', date: '2024.07', units: 1500 },
        { name: 'eÌé∏ÌïúÏÑ∏ÏÉÅ ÌåêÍµê', region: 'Í≤ΩÍ∏∞ ÏÑ±ÎÇ®', date: '2024.06', units: 1800 },
        { name: 'Ìë∏Î•¥ÏßÄÏò§ ÏùºÏÇ∞', region: 'Í≤ΩÍ∏∞ Í≥†Ïñë', date: '2024.05', units: 2500 },
        { name: 'ÏïÑÌÅ¨Î°ú Î¶¨Î≤ÑÌè¨Î†à', region: 'ÏÑúÏö∏ ÎßàÌè¨', date: '2024.04', units: 680 },
    ];

    return recentProjects.map(project => {
        // ÏßÄÏó≠Î≥Ñ Í≤ΩÏüÅÎ•† ÏãúÎÆ¨Î†àÏù¥ÏÖò
        let avgCompetition;
        if (project.region.includes('ÏÑúÏö∏')) {
            avgCompetition = 80 + Math.random() * 150;
        } else if (project.region.includes('Í≤ΩÍ∏∞')) {
            avgCompetition = 30 + Math.random() * 80;
        } else {
            avgCompetition = 5 + Math.random() * 30;
        }

        return {
            ...project,
            avgCompetition: parseFloat(avgCompetition.toFixed(1)),
            firstPriorityCompetition: parseFloat((avgCompetition * 1.5).toFixed(1)),
            specialCompetition: parseFloat((avgCompetition * 0.8).toFixed(1)),
            pricePerPyeong: Math.round(3500 + Math.random() * 2500), // ÎßåÏõê/Ìèâ
        };
    });
};

// ÏõîÎ≥Ñ Ï≤≠ÏïΩ Í≤ΩÏüÅÎ•† Ï∂îÏù¥
export const generateMonthlyCompetition = () => {
    const data = [];

    for (let year = 2020; year <= 2025; year++) {
        for (let month = 1; month <= 12; month++) {
            if (year === 2025 && month > 12) break;

            let seoul, gyeonggi, local;

            // ÏãúÏû• ÏÉÅÌô©Ïóê Îî∞Î•∏ Í≤ΩÏüÅÎ•† Î≥ÄÌôî
            if (year === 2021) {
                seoul = 150 + Math.random() * 100; // Í≥ºÏó¥Í∏∞
                gyeonggi = 80 + Math.random() * 50;
            } else if (year === 2022) {
                seoul = 50 + Math.random() * 60; // Í∏àÎ¶¨ Ïù∏ÏÉÅÍ∏∞
                gyeonggi = 20 + Math.random() * 30;
            } else if (year === 2023) {
                seoul = 30 + Math.random() * 40;
                gyeonggi = 10 + Math.random() * 20;
            } else if (year >= 2024) {
                seoul = 80 + Math.random() * 80; // ÌöåÎ≥µÍ∏∞
                gyeonggi = 40 + Math.random() * 40;
            } else {
                seoul = 60 + Math.random() * 50;
                gyeonggi = 30 + Math.random() * 30;
            }

            local = 5 + Math.random() * 15;

            data.push({
                date: `${year}.${String(month).padStart(2, '0')}`,
                year,
                month,
                seoul: parseFloat(seoul.toFixed(1)),
                gyeonggi: parseFloat(gyeonggi.toFixed(1)),
                local: parseFloat(local.toFixed(1)),
            });
        }
    }

    return data;
};

// Î∂ÑÏñëÍ∞Ä Ï∂îÏù¥ (3.3„é°Îãπ, ÎßåÏõê)
export const generateSalesPriceData = () => {
    const data = [];

    for (let year = 2015; year <= 2025; year++) {
        // ÏÑúÏö∏ Î∂ÑÏñëÍ∞Ä ÏÉÅÏäπ Ï∂îÏÑ∏
        let seoulPrice = 2200 + (year - 2015) * 250;
        if (year >= 2021) seoulPrice += 500; // Í∏âÎì±
        if (year >= 2023) seoulPrice += 300; // Í≥†Î∂ÑÏñëÍ∞Ä ÏßÄÏÜç

        let gyeonggiPrice = 1200 + (year - 2015) * 150;
        if (year >= 2021) gyeonggiPrice += 300;

        let localPrice = 800 + (year - 2015) * 80;
        if (year >= 2021) localPrice += 150;

        data.push({
            year,
            seoul: Math.round(seoulPrice + Math.random() * 200),
            gyeonggi: Math.round(gyeonggiPrice + Math.random() * 100),
            local: Math.round(localPrice + Math.random() * 50),
            seoulChange: year > 2015 ? parseFloat((Math.random() * 15 + 5).toFixed(1)) : 0,
        });
    }

    return data;
};

// ÎØ∏Î∂ÑÏñë ÌòÑÌô© Îç∞Ïù¥ÌÑ∞ (ÏßÄÏó≠Î≥Ñ)
